import * as pc from 'playcanvas'
import {AssetsLoader} from './assets-loader'



export class App {

    public app:pc.Application
	private canvas:HTMLCanvasElement
	private cubes:pc.Entity[]=[];

    public get App(): pc.Application {
        return this.app;
    };

    public Awake(): void {
        this.initApp();
        AssetsLoader.loadAssets(this.app,() => this.assetsLoaded())
    }

    
    private initApp(){
        this.canvas = this.createCanvas();

        this.app = new pc.Application(this.canvas, {
            mouse: new pc.Mouse(this.canvas),
            touch: new pc.TouchDevice(this.canvas),
            keyboard: new pc.Keyboard(window)
		});

        this.app.start();
        this.app.setCanvasFillMode(pc.FILLMODE_FILL_WINDOW);
        this.app.setCanvasResolution(pc.RESOLUTION_AUTO);
        window.addEventListener("resize", () => {
            this.app.resizeCanvas(this.canvas.width, this.canvas.height);
		});
		console.log("-----> APP INITIALIZED")
    }


    public assetsLoaded():void{
        console.log("ASSETS LOADED");
		// create camera
		var c = new pc.Entity();
		c.addComponent('camera', {
			clearColor: new pc.Color(44 / 255, 62 / 255, 80 / 255),
			farClip: 10000
		});
		this.app.root.addChild(c);

		var l = new pc.Entity();
		l.addComponent("light", {
			type: "spot",
			range: 30
		});
		l.translate(0, 10, 0);
		this.app.root.addChild(l);

		var material = new pc.StandardMaterial();
		

		// create a grid of cubes
		var SIZE = 4;
		for (var x = 0; x <= SIZE; x++) {
			for (var y = 0; y <= SIZE; y++) {
				this.createCube(2 * x - SIZE, -1.5, 2 * y - SIZE, material);
			}
		}


		// load a texture, create material and apply to box
		
		var asset:pc.Asset = this.app.assets.find("panel2");
		material.diffuseMap = asset.resource;
		material.update();
		
		//TExto
		this.addTexto(this.app);

    }

	private  createCube   (x:number, y:number, z:number, material:pc.StandardMaterial) {
		var cube = new pc.Entity();
		cube.addComponent("model", {
			type: "box",
			material: material
		});
		cube.setLocalScale(1, 1, 1);
		cube.translate(x, y, z);
		this.app.root.addChild(cube);
		this.cubes.push(cube);
	};

    private createCanvas():HTMLCanvasElement{
        const canvas:HTMLCanvasElement = document.createElement('canvas')
        canvas.setAttribute('width', '500px')
        canvas.setAttribute('height', '500px')
        document.body.appendChild(canvas);
        return canvas;
    }

	addTexto(app:pc.Application){
		var asset:pc.Asset = this.app.assets.find("font");
		var texto = new pc.Entity("texto");
		texto.addComponent("element", {
			anchor: [0.5, 0.5, 0.5, 0.5],
			autoWidth: true,
			autoHeigth: true,
			fontSize: 0.2,
			color: new pc.Color(0.9, 0.9, 0.9),
			Alignment:[0,1],
			pivot: [0.5, 0.5],
			//shadowColor: new pc.Color(0, 0, 0),
			//shadowOffset: new pc.Vec2(0.25, -0.25),
			text: "AAAAAAAAA",
			type: pc.ELEMENTTYPE_TEXT
		});
		if(texto.element)
			texto.element.fontAsset = asset.id;
		texto.tags.add("texto");
		texto.tags.add("scalable");
		texto.setLocalPosition(0.4,0.6,-2);
		texto.setLocalEulerAngles(75, 0, 0);
		app.root.addChild(texto);
		console.log("texto creado");
		
	}
}